*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
@media (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  font-family: "Lato", sans-serif;
  line-height: 1.7;
  color: #777;
  padding: 3rem;
}
@media (max-width: 56.25em) {
  body {
    padding: 0;
  }
}

::selection {
  background-color: #55c57a;
  color: #fff;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translate(1.5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translate(-1.5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
.text-align--center {
  text-align: center;
}

.margin-bottom--small {
  margin-bottom: 1.5rem !important;
}

.margin-bottom--medium {
  margin-bottom: 4rem !important;
}
@media (max-width: 56.25em) {
  .margin-bottom--medium {
    margin-bottom: 3rem;
  }
}

.margin-bottom--big {
  margin-bottom: 8rem;
}
@media (max-width: 56.25em) {
  .margin-bottom--big {
    margin-bottom: 5rem;
  }
}

.margin-bottom--huge {
  margin-bottom: 10rem !important;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 1.5rem 4rem;
  border-radius: 10rem;
  transition: transform 0.2s, box-shadow 0.2s;
  position: relative;
  font-size: 1.6rem;
  border: none;
  cursor: pointer;
}
.btn--white {
  background-color: #fff;
  color: #777;
}
.btn--white::after {
  background-color: #fff;
}
.btn--green {
  background-color: #55c57a;
  color: #fff;
}
.btn--green::after {
  background-color: #55c57a;
}
.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);
}
.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.6);
}
.btn::after {
  content: "";
  display: inline-block;
  z-index: -1;
  width: 100%;
  height: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.4s, opacity 0.4s;
}

.btn-text:link, .btn-text:visited {
  text-decoration: none;
  color: #55c57a;
  display: inline-block;
  padding: 3px;
  border-bottom: 1px solid #55c57a;
  transition: all 0.2s;
  font-size: 1.6rem;
}
.btn-text:hover {
  background-color: #55c57a;
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}
.btn-text:active {
  transform: translateY(0);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.title {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  background-image: linear-gradient(to right, #7ed56f, #28b485);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  letter-spacing: 2px;
  transition: all 0.2s;
}
@media (max-width: 56.25em) {
  .title {
    font-size: 3rem;
  }
}
@media (max-width: 37.5em) {
  .title {
    font-size: 2.5rem;
  }
}
.title:hover {
  transform: skewY(2deg) skewX(15deg) scale(1.1);
  text-shadow: 0.5rem 1rem 2rem rgba(0, 0, 0, 0.2);
}

.title-secondary {
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 2rem;
}

.paragraph {
  font-size: 1.6rem;
  margin-bottom: 3rem;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(15px);
  z-index: 300;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}
.popup__content {
  position: absolute;
  width: 75%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.25);
  opacity: 0;
  background-color: #fff;
  display: table;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.2);
  transition: all 0.5s 0.2s;
}
@media (max-width: 75em) {
  .popup__content {
    display: block;
  }
}
.popup__left {
  width: 33.333%;
  display: table-cell;
}
@media (max-width: 75em) {
  .popup__left {
    display: block;
    width: 100%;
  }
}
@media (max-width: 56.25em) {
  .popup__left {
    display: none;
  }
}
.popup__right {
  width: 66.667%;
  display: table-cell;
  padding: 3rem 5rem;
  vertical-align: middle;
}
@media (max-width: 75em) {
  .popup__right {
    display: block;
    width: 100%;
  }
}
@media (max-width: 37.5em) {
  .popup__right {
    padding: 1rem 1.5rem;
  }
}
.popup__img {
  width: 100%;
  display: block;
}
@media (max-width: 75em) {
  .popup__img {
    display: inline-block;
    width: 49%;
  }
}
.popup__text {
  font-size: 1.4rem;
  margin-bottom: 4rem;
  column-count: 2;
  column-gap: 4rem;
  column-rule: 1px solid #eee;
  hyphens: auto;
}
@media (max-width: 37.5em) {
  .popup__text {
    column-count: 1;
    margin-bottom: 2rem;
  }
}
.popup__close {
  display: inline-block;
  position: absolute;
  top: 2.5rem;
  right: 2.5rem;
  color: #777;
  text-decoration: none;
  font-size: 3rem;
  line-height: 1;
  transition: all 0.2s;
}
.popup__close:hover {
  color: #55c57a;
}
.popup:target {
  visibility: visible;
  opacity: 1;
}
.popup:target .popup__content {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.composite {
  position: relative;
}
.composite__img {
  width: 55%;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.4);
  border-radius: 2px;
  position: absolute;
  z-index: 5;
  transition: all 0.2s;
  outline-offset: 2rem;
}
@media (max-width: 56.25em) {
  .composite__img {
    float: left;
    position: relative;
    width: 33.333%;
    box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, 0.2);
  }
}
.composite__img--p1 {
  top: -2rem;
  left: 0;
}
@media (max-width: 56.25em) {
  .composite__img--p1 {
    top: 0;
    transform: scale(1.2);
  }
}
.composite__img--p2 {
  top: 2rem;
  right: 0;
}
@media (max-width: 56.25em) {
  .composite__img--p2 {
    top: -1rem;
    z-index: 15;
    transform: scale(1.3);
  }
}
.composite__img--p3 {
  top: 10rem;
  left: 20%;
}
@media (max-width: 56.25em) {
  .composite__img--p3 {
    top: 1rem;
    left: 0;
    transform: scale(1.1);
  }
}
.composite__img:hover {
  z-index: 6;
  transform: scale(1.05) translateY(-0.5rem);
  box-shadow: 0 2.5rem 4rem rgba(0, 0, 0, 0.5);
  outline: solid 1.5rem #55c57a;
}
.composite:hover .composite__img:not(:hover) {
  transform: scale(0.95);
}

.feature {
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 1.5rem;
  padding: 2.5rem;
  border-radius: 3px;
  text-align: center;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
  transition: transform 0.3s;
}
@media (max-width: 56.25em) {
  .feature {
    padding: 2rem;
  }
}
.feature:hover {
  transform: translateY(-1.5rem) scale(1.03);
}
.feature__icon {
  font-size: 6rem;
  margin-bottom: 0.5rem;
  color: transparent;
  background-image: linear-gradient(to right, #7ed56f, #28b485);
  background-clip: text;
  -webkit-background-clip: text;
}
@media (max-width: 56.25em) {
  .feature__icon {
    margin-bottom: 0;
  }
}

.tour {
  height: 52rem;
  position: relative;
  perspective: 150rem;
  -moz-perspective: 150rem;
}
.tour__side {
  height: 52rem;
  position: absolute;
  width: 100%;
  transition: transform 0.8s ease;
  backface-visibility: hidden;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
}
.tour__side--back {
  transform: rotateY(180deg);
}
.tour__side--yellow {
  background-image: linear-gradient(to right bottom, #ffb900, #ff7730);
}
.tour__side--green {
  background-image: linear-gradient(to right bottom, #7ed56f, #28b485);
}
.tour__side--blue {
  background-image: linear-gradient(to right bottom, #2998ff, #5643fa);
}
.tour:hover .tour__side--front {
  transform: rotateY(-180deg);
}
.tour:hover .tour__side--back {
  transform: rotateY(0);
}
.tour__img {
  height: 23rem;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  background-blend-mode: screen;
  background-size: cover;
  border-radius: 3px 3px 0 0;
}
.tour__img--sea {
  background-image: linear-gradient(to right bottom, #ffb900, #ff7730), url(../img/nat-5.jpg);
}
.tour__img--forest {
  background-image: linear-gradient(to right bottom, #7ed56f, #28b485), url(../img/nat-6.jpg);
}
.tour__img--snow {
  background-image: linear-gradient(to right bottom, #2998ff, #5643fa), url(../img/nat-7.jpg);
}
.tour__title {
  font-size: 2.8rem;
  font-weight: 300;
  position: absolute;
  top: 12rem;
  right: 2rem;
  text-transform: uppercase;
  width: 75%;
  color: #fff;
  text-align: right;
}
.tour__span {
  padding: 1rem 1.5rem;
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
}
.tour__span--yellow {
  background-image: linear-gradient(to right bottom, rgba(255, 185, 0, 0.85), rgba(255, 119, 48, 0.85));
}
.tour__span--green {
  background-image: linear-gradient(to right bottom, rgba(126, 213, 111, 0.85), rgba(40, 180, 133, 0.85));
}
.tour__span--blue {
  background-image: linear-gradient(to right bottom, rgba(41, 152, 255, 0.85), rgba(86, 67, 250, 0.85));
}
.tour__list ul {
  font-size: 1.5rem;
  width: 80%;
  margin: 0 auto;
  list-style-type: none;
  text-align: center;
}
.tour__list ul li {
  padding: 1rem;
}
.tour__list ul li:not(:last-child) {
  border-bottom: 1px solid #eee;
}
.tour__back {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
}
.tour__price-box {
  color: #fff;
  margin-bottom: 8rem;
}
.tour__only {
  font-size: 1.4rem;
