    top: 4rem;
    right: 4rem;
  }
}
@media (max-width: 37.5em) {
  .navigation__btn {
    top: 3rem;
    right: 3rem;
  }
}
.navigation__checkbox {
  display: none;
}
.navigation__bg {
  position: fixed;
  width: 6rem;
  height: 6rem;
  top: 6.5rem;
  right: 6.5rem;
  background-image: radial-gradient(#7ed56f, #28b485);
  border-radius: 50%;
  z-index: 150;
  transition: transform 0.8s cubic-bezier(0.83, 0, 0.17, 1);
}
@media (max-width: 56.25em) {
  .navigation__bg {
    top: 4.5rem;
    right: 4.5rem;
  }
}
@media (max-width: 37.5em) {
  .navigation__bg {
    top: 3.5rem;
    right: 3.5rem;
  }
}
.navigation__nav {
  position: fixed;
  height: 100vh;
  opacity: 0;
  top: 0;
  left: 0;
  width: 0;
  z-index: 180;
  transition: all 0.8s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}
.navigation__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
}
.navigation__item {
  margin: 1rem;
}
.navigation__link:link, .navigation__link:visited {
  color: #fff;
  text-decoration: none;
  font-size: 3rem;
  font-weight: 300;
  padding: 1rem 2rem;
  display: inline-block;
  background-image: linear-gradient(120deg, transparent 0%, transparent 50%, #fff 50%);
  background-size: 230%;
  transition: all 0.4s;
}
.navigation__link:hover, .navigation__link:active {
  color: #55c57a;
  background-position: 100%;
  transform: translateX(1rem);
}
.navigation__link span {
  margin-right: 1.5rem;
}
.navigation__checkbox:checked ~ .navigation__bg {
  transform: scale(80);
}
.navigation__checkbox:checked ~ .navigation__nav {
  width: 100%;
  opacity: 1;
}
.navigation__icon {
  position: relative;
  margin-top: 3.5rem;
}
.navigation__icon, .navigation__icon::after, .navigation__icon::before {
  display: inline-block;
  width: 3rem;
  height: 2px;
  background-color: #333;
  transition: all 0.2s;
}
.navigation__icon::after, .navigation__icon::before {
  content: "";
  position: absolute;
  left: 0;
}
.navigation__icon::after {
  top: 0.8rem;
}
.navigation__icon::before {
  top: -0.8rem;
}
.navigation__btn:hover .navigation__icon::after {
  top: 1rem;
}
.navigation__btn:hover .navigation__icon::before {
  top: -1rem;
}
.navigation__checkbox:checked + .navigation__btn .navigation__icon {
  background-color: transparent;
}
.navigation__checkbox:checked + .navigation__btn .navigation__icon::after {
  transform: rotate(135deg);
  top: 0;
}
.navigation__checkbox:checked + .navigation__btn .navigation__icon::before {
  transform: rotate(-135deg);
  top: 0;
}

